.wrapper {
    background-color: #ad93fb;
    height: 60vh;
    display: flex;
    justify-content: center;
    padding: 1%;
}

.projectsCard {
    box-sizing: border-box;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border: 1px solid;
    padding: 1%;
}

.projectsCardName {
    height: 80px;
    border: 1px dashed grey;
    display: flex;
    justify-content: center;
    width: 100%;
}
.projectsCardName span {
    display: inline-block;
    align-self: center;
    font-weight: 700;
    font-size: 30px;
}

.projectsField {
    width: 100%;
    display: flex;
    justify-content: space-between;
}