.skillCard {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    min-width:30%;
    border: 1px solid;
}

.logo img {
    padding-top: 50%;
    height: 60px;
}

.skillTitle {
    font-size:20px;
    font-weight: 600;
    align-self: center;
    padding: 10px;
}

.skillDescription {
    width: 90%;
    height: 70px;
    text-align: center;
    border: 1px solid blue;
    margin: 5%;
}